import './scss/style.scss';
import Body from './js/components/body';
import Api from './js/api';
import Localization from './js/localization';
import { backgroundQuery, Localstorage, setIntervalStart } from './js/utilities';

const initObj = {
  lang: 'en',
  temparatureFormat: 'c',
  respond: '',
};

const localStorage = new Localstorage('app_init');
const state = (localStorage.restoreData() === false) ? initObj : localStorage.restoreData();

const lcl = new Localization(state.lang);
const api = new Api(lcl, state.lang);
const body = new Body(lcl, api, state.temparatureFormat);

const init = () => {
  api.geoPosition().then((data) => {
    body.stat = data;
    body.init();
  });
};

init();

window.addEventListener('DOMContentLoaded', () => {
  document.cookie = 'HttpOnly;Secure;SameSite=Strict';
  setIntervalStart(api, body, 60);

  body.panel.optionsCont.addEventListener('click', ({ target }) => {
    if (target.classList.contains('options__bgupdate')) {
      api.background.draw = true;
      api.background.query = backgroundQuery(api.respond.weather.stateVal);
      api.getBackground();
    }

    if (target.hasAttribute('type')) {
      state.temparatureFormat = (state.temparatureFormat === 'c') ? 'f' : 'c';
      body.tempFormat = state.temparatureFormat;
      Object.values(target.parentNode.children).forEach((elem) => {
        elem.classList.toggle('active');
      });
      body.tempFormatter();
    }
  });

  body.search.searchCont.addEventListener('click', (event) => {
    event.preventDefault();
    if (event.target.classList.contains('submit')) {
      const q = body.search.searchCont.children[0].children[0].value;
      try {
        if (/[a-z\u0400-\u04FF ]/g.test(q) === false) throw new Error('Query shouldn`t have any special chars. Please type city name. For example: London');
        api.weather.city = q;
        api.translateQuery(q, 'en').then((translatedCity) => {
          const city = translatedCity[0];
          if (!city || city === '') throw new Error('Your request was rejected. Check for errors your query.');
          api.weather.city = city;
        }).then(() => api.getLocationWeatherFull()).then((data) => {
          if (api.setInterval === null) {
            setIntervalStart(api, body, 60);
          }
          body.stat = data;
          body.update();
          body.search.searchCont.children[0].children[0].value = '';
        })
          .catch(() => {
            api.weather.city = api.weather.cityBackup;
          });
      } catch (err) {
        global.alert(err);
      }
    }
  });

  body.panel.optionsCont.addEventListener('change', ({ target }) => {
    if (target.classList.contains('lang')) {
      state.lang = target.value;
      lcl.lang = target.value;
      api.translate.langTo = target.value;
      body.localize();
    }
  });
}, false);

window.addEventListener('unload', () => {
  localStorage.saveData(state);
});
